    <ul v-if="navDataProp" class="list__wrapper inner">
      <li
        class="items__wrapper"
        v-for="(item, i) in menus[navDataProp].length > 0
          ? menus[navDataProp][0]
          : menus[navDataProp]"
        :key="i"
      >
        <dl class="item__list">
          <dt class="item__title">{{ item.title }}</dt>
          <dd
            class="item__content"
            v-for="(item__inner, i) in item.items"
            :key="i"
          >
