<style lang="scss" scoped>
.nav__menu {
  position: fixed;
  top: 128px;
  left: 0;
  z-index: 99;
  width: 100%;
  max-height: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #2c2a29;
  transition: 0.6s all;
  overflow: hidden;

  .list__wrapper {
    display: flex;
    flex-wrap: wrap;
    color: #fff;
    padding: 2rem;

    .items__wrapper {
      width: 220px;

      .item__list {
        display: flex;
        flex-direction: column;
        align-items: start;

        .item__title {
          display: inline-block;
          padding: 3px 0 12px;
          cursor: pointer;
        }
        .item__title:hover {
          text-decoration: underline;
        }

        .item__content {
          display: inline-block;
          padding: 5px 0;
          font-size: 0.75rem;
          color: #999;
          cursor: pointer;
        }
        .item__content:hover {
          color: #669900;
          text-decoration: underline;
        }
      }
    }
  }

  .nav__footer {
    display: flex;
    justify-content: center;

    .list__wrapper {
      display: flex;
      flex-direction: column;
      padding: 2rem;
      font-size: 0.75rem;

      .item__content {
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;

        h3:nth-child(1) {
          font-weight: 700;
          color: #999;
        }
        span:nth-child(2) {
          color: #669900;
        }
      }

      .item__content:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.nav__menu.active {
  max-height: 999px;
}
</style>
